<template>
  <div class="home">
    <title>111</title>
    <register ref="registerDialog"></register>
      <div class="content">
        <div class="login-form" :style="'height:'+formHeight+'px'">
          <div class="project-name" style="text-shadow: 1px 1px 3px rgba(43,74,120,0.52);">{{$global.PROJECT_NAME}}</div>
          <el-form :model="editForm" status-icon :rules="rules" ref="editForm" label-width="100px">
            <div class="login-input">
              <el-input v-model="editForm.loginName" placeholder="请输入登录名"></el-input>
            </div>
            <div class="login-input">
              <el-input type="password" v-model="editForm.password" placeholder="请输入密码" autocomplete="off"></el-input>
            </div>
            <div v-if="showRoles" class="login-input"  style="text-align: center">
              <el-radio-group v-model="editForm.roleName">
                <el-radio v-for="role in roleList" :key="role.roleName" :label="role.roleName">{{role.roleName}}</el-radio>
              </el-radio-group>
            </div>
            <div class="login-input">
                <span v-if="showRegister" class="register-span">
                    <small>没有账号?&nbsp;&nbsp;</small><a class="register-a" @click="register()">立即注册</a>
                </span>
              <el-button style="width: 100%;" :class="[{'btn-top': !(showRegister||showRoles)}]" type="primary" @click="login">登录</el-button>
            </div>
          </el-form>
        </div>
    </div>
  </div>
</template>
<script>
  import {mapActions} from "vuex";
  import register from './register'
  export default {
    components:{register},
    data() {
      return {
        roleList:[],
        editForm: {
          roleName:'',
          loginName: '',
          password: ''
        }
      };
    },
    computed:{
      showRoles(){
        const showRoles = this.$global.CONFIG.showRoles;
        this.editForm.roleName=showRoles?"管理员":'';
        return showRoles;
      },
      showRegister(){
        return this.$global.CONFIG.showRegister
      },
      formHeight(){
        return this.showRoles?350:320
      }
    },
    mounted() {
      this.getRoleList();
    },
    methods: {
      ...mapActions(['commitConfig','commitDictList','commitNoticeList','commitLoginInfo']),
      //打开弹窗
      register(){
        this.$refs.registerDialog.open({title:'用户注册'});
      },
      initData() {
        const loading = this.$loading({
          lock: true,
          text: '请稍后,登录中...',
          spinner: 'el-icon-loading',
          background: 'rgba(0,0,0,0.25)'
        });
        //加载配置数据
        this.commitConfig().then(res => {
          //加载数据字典
          this.commitDictList().then(res => {
            //获取登录信息
            this.commitLoginInfo().then(res => {
              let getters = this.$store.getters;
              console.log("getters:",getters)
              this.goHomePage();
              loading.close();
            })
          })
        })
      },
      goHomePage() {
        this.$router.push('/main');
      },
      login() {
        this.subLogin();
      },
      getRoleList(){
        const that = this;
        that.$request.doGet("/api/sys/getRoleList").then(res => {
          if (res.isOk) {
            that.roleList = res.list;
          }
        });
      },
      subLogin() {
        this.$request.doPost("/api/login/userLogin", this.editForm).then(res => {
          if (res.isOk) {
            this.initData();//初始化数据
            this.showSuccess(res.info);
          } else {
            this.showWarn(res.info);
          }
        });
      }
    }
  }
</script>
<style scoped>
  .btn-top{
    margin-top: 20px
  }
  .home{
    width: 100%;
    height: 100vh;
    background: url("/static/login/images/login.jpg") center center no-repeat;
    background-size: 100% 100%;
  }
  .content {
    width: 350px;
    margin: 0 auto; /*水平居中*/
    position: relative;
    top: 50%; /*偏移*/
    transform: translateY(-50%);
  }
  .title{
    padding: 20px;
    text-align: center;
    color: #6a6c6f;
  }
  .login-form{
    border-radius: 10px;
    width: 350px;
    background: rgba(255, 255, 255, 0.85);
  }
  .login-input{
    margin: 25px;
  }
  .register-span{
    display: block;
    padding-right: 3px;
    padding-bottom: 5px;
    float: right
  }
  .register-a{
    cursor: pointer;
  }

  .project-name{
    padding:10px;
    font-size: 30px;
    color: #324262;
    font-weight: bold;
    text-align: center;
    margin-bottom: 10px;
    /*margin: 0 auto; !*水平居中*!*/
  }
</style>



