<!-- 【用户信息】弹窗页面 -->
<template>
  <el-dialog :title="dialog.title" center style="margin-top: 200px;text-align: center" width="500px" :visible.sync="dialog.open">
    <el-form :model="sysUser" ref="sysUserForm" :rules="rules" label-width="100px">
      <el-form-item>
        <el-input v-model="sysUser.loginName" placeholder="请输入登录名" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item>
        <el-input v-model="sysUser.password" type="password" placeholder="请输入密码" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item>
        <el-input v-model="sysUser.prePassword" type="password" placeholder="请输入确认密码" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button size="small" @click="cancel">取 消</el-button>
      <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitRegister('sysUserForm')">提 交</el-button>
    </div>
  </el-dialog>
</template>

<script>
  import suiPhoto from "@/components/sui-photo/sui-photo";
  export default {
    components: {suiPhoto},
    name: "sysUserForm",
    data() {
      return {
        //弹窗设置
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //表单数据
        sysUser: {
          photo: "",
          loginName: "",
          prePassword:"",
          password: ""
        },
        //表单校验
        rules: {
          photo: [{required: true, message: '请输入用户头像', trigger: 'blur'}],
          loginName: [{required: true, message: '请输入登录名', trigger: 'blur'}],
          password: [{required: true, message: '请输入密码', trigger: 'blur'}],
          prePassword: [{required: true, message: '请输入确认密码', trigger: 'blur'}],
        }
      }
    },
    methods: {

      //打开弹窗
      open(option) {
        this.showDialog(option.title, false);
      },
      //取消弹窗
      cancel: function () {
        this.dialog.open = false
      },
      //提交表单
      submitRegister: function (formName) {
        const that = this;
        that.$refs[formName].validate((valid) => {
          if (valid) {//表达校验
            if(that.sysUser.password!=that.sysUser.prePassword){
              this.showWarn("两次密码不一致!")
              return false
            }
            that.$request.doPost('/api/sysUser/register',that.sysUser).then(res => {
              if (res.isOk) {
                that.dialog.open = false;
                that.showSuccess("注册成功");
                that.$emit('reloadList');
              }else{
                that.showWarn(res.info);
              }
            });
          }
        });
      },
      //显示弹窗
      showDialog(title, disabled) {
        this.resetForm(this.$refs.sysUserForm);//重置表单
        this.dialog.open = true;//打开弹窗
        this.dialog.title = title;//设置标题
        this.dialog.disabled = disabled;//是否可编辑
      },
    }
  }
</script>

<style scoped>
</style>



